.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #f8f8f9;
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 45px;
  line-height: 45px;
  font-size: 16px;
  text-align: center;
}
.tab .tab-item .router-link-active {
  display: block;
  color: red;
  position: relative;
}
.tab .tab-item .router-link-active:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 2px solid red;
  content: ' ';
}



